import contents from './news_contents.js'
export default {
  components: {

  },
  data() {
    return {
        items: contents
    };
  },
  methods: {
      openInNewTap(url) {
          window.open(url, '_blank').focus()
      }
  },
  computed: {
      formattedItems() {
          return this.items.reduce((c, n, i) => {
              if (i % 3 === 0) c.push([]);
              c[c.length - 1].push(n);
              return c;
          }, []);
      }
  }
};
